html,body{height:100%;width:100%;background:#A8CDF7;overflow:hidden}#app{color:white;position:fixed;top:0;bottom:0;left:0;right:0;-webkit-transition-duration:.3s;transition-duration:.3s}.right-block{position:absolute;top:0;bottom:0;right:0;width:40%;min-width:400px}.main-block{position:absolute;top:0;bottom:0;left:0;right:40%;overflow:hidden;-webkit-transition-duration:.3s;transition-duration:.3s}.main-block.full-width{right:0%}.video-container{position:absolute;z-index:2000;top:0;bottom:0;left:0;right:0;background:rgba(0,0,0,0.6)}.video-container iframe{position:absolute;top:50%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%)}.fade-transition{-webkit-transition:all .3s ease;transition:all .3s ease;opacity:1}.fade-enter,.fade-leave{opacity:0}



